import { useState, useEffect } from "react"
import { Link } from "react-router-dom"
import { Button, Dropdown, Menu } from "antd"
import { DollarOutlined, UserOutlined, SettingOutlined, LogoutOutlined, ShoppingCartOutlined, ShopFilled, CrownOutlined } from "@ant-design/icons"

import axios from "axios"

const USER_SERVICE = process.env.REACT_APP_API_USER_SERVICE || 'https://accommerce.cuongdm.tech/user-service/api'

const UserMenu = props => {
    const [username, setUsername] = useState('')
    const [user, setUser] = useState('')
    const [roles, setRoles] = useState(['customer'])

    useEffect(async () => {
        const token = localStorage.getItem('token')

        const { data: userData } = await axios({
            method: 'get',
            url: `${USER_SERVICE}/users/self`,
            headers: {
                'Authorization': token,
                'Content-Type': 'text/plain'
            }
        })
        const { data, message, success } = userData
        if (!success) throw new Error(message)

        const { username, firstName, lastName, roles } = data
        setUsername(username)
        setUser(`${firstName} ${lastName}`)
        setRoles(roles)
    })

    const handleMenuClick = e => {
        const { key } = e
        switch (key) {
            case 'self': {
                break
            }

            case 'admin': {
                break
            }

            case 'seller': {
                break
            }

            case 'setting': {
                break
            }

            case 'logout': {
                localStorage.removeItem('token')
                window.location.reload()
                break
            }
        }
    }

    const menu = (
        <Menu onClick={handleMenuClick}>
            <Menu.Item key="self" icon={<UserOutlined />}>
                {user}
            </Menu.Item>
            {roles.includes('admin') &&
                <Menu.Item key="admin" icon={<CrownOutlined />}>
                    <a href="/admin">Quản trị</a>
                </Menu.Item>
            }
            {roles.includes('seller') &&
                <Menu.Item key="seller" icon={<ShopFilled />}>
                    <a href="/store">Cửa hàng</a>
                </Menu.Item>
            }
            <Menu.Item key="setting" icon={<SettingOutlined />}>
                Cài đặt
            </Menu.Item>
            <Menu.Item key="logout" icon={<LogoutOutlined />}>
                Đăng xuất
            </Menu.Item>
        </Menu>
    )

    return (
        <Dropdown overlay={menu}>
            <Button size="large" shape="round" shape="round" id="btn_user">
                {username}
            </Button>
        </Dropdown>
    )
}

export default UserMenu